import React from "react";
import logo from "../../assets/images/2gather.jpg";
import { isValidEmail } from "../../platform/validatior";
import eyeIcon from "../../assets/images/close-eye.png";

import "./style.scss";

class SignIn extends React.Component {
  state = {
    form: {
      email: "",
      password: ""
    },
    focus: {
      email: false,
      password: false
    },
    passToggle: false,
    submitted: false
  };

  submit = async e => {
    e.preventDefault();
    const {
      form: { email, password }
    } = this.state;

    this.setState({
      submitted: true
    });

    if (isValidEmail(email) && password) {
      try {
        const response = await fetch("https://2.evollai.com/api/auth/login", {
          method: "POST",
          body: JSON.stringify({
            email,
            password,
            osType: 3
          }),
          headers: {
            "Content-Type": "application/json",
            "Accept-Language": "*",
            language: 1
          }
        });
        const result = await response.json();
        if (result) {
          localStorage.setItem("authToken", result.data.authToken);
          window.routerHistory.push("/topic-suggestion");
        }
      } catch (error) {
        console.log(error);
      }
    }
  };

  change = ({ target: { name, value } }) => {
    const { form } = this.state;
    form[name] = value;
    this.setState({ form });
  };

  onFocusEmail = () => {
    this.setState({
      focus: {
        email: true
      }
    });
  };

  onBlurEmail = (e, key) => {
    this.setState({
      focus: {
        email: e.target.value !== ""
      }
    });
  };

  onFocusPassword = () => {
    this.setState({
      focus: {
        password: true
      }
    });
  };

  onBlurPassword = () => {
    this.setState({
      focus: {
        password: e.target.value !== ""
      }
    });
  };

  togglePassword = () => {
    this.setState({
      passToggle: !this.state.passToggle
    });
  };

  render() {
    const { form, focus, passToggle, submitted } = this.state;
    return (
      <div className="sign-in">
        <div className="sign-in-form">
          <div className="sign-in-form-container">
            <h2>Sign in</h2>
            <p>
              Enter your email and password and login in your <br /> account
            </p>
            <form>
              <div className="fields">
                <div className="G-field">
                  <label
                    className={`G-form-label  ${
                      focus.email ? "G-label-animated" : ""
                    }`}
                    htmlFor="signin-email"
                  >
                    Email
                  </label>
                  <input
                    type="email"
                    onChange={this.change}
                    name="email"
                    onFocus={this.onFocusEmail}
                    onBlur={this.onBlurEmail}
                    className={
                      submitted && !isValidEmail(form.email)
                        ? "G-invalid-input"
                        : ""
                    }
                  />

                  <span
                    className={`input-border ${
                      focus.email ? "input-border-animated" : ""
                    }`}
                  ></span>
                </div>
                <div className="G-field">
                  <label
                    className={`G-form-label  ${
                      focus.password ? "G-label-animated" : ""
                    }`}
                    htmlFor="signin-password"
                  >
                    Password
                  </label>
                  <input
                    name="password"
                    type={!passToggle ? "password" : "text"}
                    onChange={this.change}
                    onFocus={this.onFocusPassword}
                    onBlur={this.onBlurPassword}
                    className={
                      submitted && !form.password ? "G-invalid-input" : ""
                    }
                  />

                  <span
                    className={`input-border ${
                      focus.password ? "input-border-animated" : ""
                    }`}
                  ></span>
                  <img
                    src={eyeIcon}
                    alt="closed"
                    className="eye-icon"
                    onClick={this.togglePassword}
                  />
                </div>
              </div>

              <button className="G-button" onClick={this.submit}>
                Sign in
              </button>
            </form>
          </div>
        </div>
        <div className="logo">
          <img src={logo} alt="2gather" />
        </div>
      </div>
    );
  }
}

export default SignIn;
